@font-face {
  font-family: "oswaldregular";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "oswaldmedium";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  display: flex;
  flex-direction: column;
  font-family: "oswaldmedium", sans-serif;
}

.page__body {
  min-height: 100%;
  margin: 0;
}

.main-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 280px;
  min-height: 65px;
  margin: 0 auto;
  padding: 0 20px;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-header {
    width: 708px;
    height: 100px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    width: 1220px;
    height: 160px;
    padding: 0 110px;
  }
}

.main-header__logo {
  text-decoration: none;
}

.main-header__logo-image {
  padding-top: 10px;
}

.main-header__logo-text {
  margin-left: 53px;
  padding-bottom: 7px;
}

.button {
  color: #ffffff;
  background-color: #68b738;
  font-family: "oswaldregular", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}

.main-nav {
  min-height: 65px;
}

@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 440px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    width: 500px;
    background-color: #68b738;
  }
}

.main-nav__toggle {
  padding: 20px 0;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: -1px;
  width: 26px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__open-burger {
  display: none;
}

.main-nav__list {
  margin: 0;
  padding: 0;
}

.main-nav__item {
  position: relative;
  text-transform: uppercase;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 170px;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    margin-left: 35px;
  }
}

@media (min-width: 768px) {
  .main-nav__item:first-child {
    width: 70px;
  }
}

.main-nav__link {
  display: block;
  padding: 23px 0;
  color: #000000;
  background-color: #ffffff;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  border-top: 1px solid #d9d9d9;
  box-sizing: border-box;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    color: #ffffff;
    background-color: #68b738;
  }
}

.main-nav__item:last-child .main-nav__link {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .main-nav__item:last-child .main-nav__link {
